.profile-wrapper {
    width: 100%;
    max-width: 43em;
    min-height: calc(100vh - 5rem);
    padding: 0 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.my-photo {
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
    margin-bottom: 2rem;
}

.description,
.quote {
    font-size: 0.85rem;
    line-height: 1.6;
    text-align: justify;
}

.description {
    padding: 0 0.5rem;
    color: #121940;
}

.description span {
    font-weight: bold;
}

.quote {
    font-weight: 500;
    margin: 1.5rem 0;
    padding: 1rem 1.25rem;
    width: 100%;
    font-style: italic;
    background-color: #FFFADD;
    color: #12194088;
    border-radius: 0.5rem;
}

.social {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #12194088;
}

.follow {
    font-size: 0.85rem;
    text-align: center;
    margin-bottom: 0.5rem;
}

.icons-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.social-icon {
    width: 1.15rem;
    height: auto;
    margin: 0.5rem;
    opacity: 0.3;
}