.nav {
    width: 100%;
    min-height: 5rem;
    position: sticky;
    top: 0;
    -moz-user-select: none;
    -webkit-user-select: none;
    z-index: 99;
}

.container {
    width: 100%;
    background-color: #fff;
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.wrapper {
    width: 100%;
    height: 5rem;
    max-width: 45em;
    padding: 0 1.5rem;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #121940;
    position: relative;
    z-index: 1;
}

.hamburger {
    cursor: pointer;
    outline: 0;
    border: 0;
    background-color: transparent;
}

.hamburger:hover {
    color: #00CEB8;
}

.bars {
    font-size: 1.75rem;
}

.logo {
    font-family: Roboto Slab;
    font-weight: bold;
    font-size: 1.75rem;
}

.logo span {
    color: #00CEB8;
}

.logo:hover {
    cursor: pointer;
}

.menu-wrapper {
    position: absolute;
    top: 5rem;
    right: -100vw;
    width: 100%;
    height: auto;
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.show-menu {
    right: 0;
    opacity: 1;
}

.menu {
    padding: 1rem 0;
    list-style: none;
    text-align: center;
}

.menu-item {
    padding: 1rem 0;
    display: flex;
    align-items: center;
    font-size: 0.85rem;
    text-decoration: none;
    color: #121940;
}

.menu-item:active,
.menu-item:visited {
    color: #121940;
}

.menu-item:hover {
    color: #00CEB8;
}

.space {
    margin-right: 0.5rem;
    font-size: 1.25rem;
}